@import url(https://fonts.googleapis.com/css2?family=Proza+Libre&display=swap);
*,*::after,*::before{padding:0;margin:0;font-family:"Proza Libre",Arial,Helvetica,sans-serif}body,.App{height:100vh;background-color:blue;background-image:url("https://images.unsplash.com/photo-1511593358241-7eea1f3c84e5?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=967&q=80");background-size:cover;flex-direction:column}.flex-center{display:flex;justify-content:center;align-items:center}.fc{flex-direction:column}button{width:30vmin;height:8vmin;margin-top:1%;border-radius:20px}.green{color:#39ab39 !important;text-shadow:2px 2px #260d80 !important}.winner{width:100%;height:40vh;background-color:#000;font-size:10em;color:#fff;position:absolute;justify-self:center;z-index:1000;transition:transform 200ms ease-out;transform:scale(0, 1)}.show-winner{transform:scale(1, 1)}.GameBoard{height:70vmin;width:63vmin;background:linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6));flex-direction:column;margin:0px 20px;border-radius:20px}.GameBoard h2{margin-bottom:4%;color:#fff;letter-spacing:2px;text-shadow:2px 2px #260d80}.grids{display:grid;grid-template-columns:repeat(10, 1fr);grid-template-rows:repeat(10, 1fr);height:56vmin;width:56vmin}.squares{background-color:#ddd;height:.15625 vmin;width:.15625 vmin;border:1px solid #000}.squares .target::before{content:"";background-color:rgba(0,0,255,.8);width:100%;height:100%;border-radius:50%;display:block;position:relative;animation:ripple ease-in 300ms;opacity:0}.squares .target{height:50%;width:50%;border-radius:50%;background-color:red}@keyframes ripple{from{transform:scale(1);opacity:100%}to{transform:scale(4);opacity:0}}.ship-wrapper{position:absolute;top:20%;left:5%}.ship-wrapper *{margin:.4em 0}.ship-wrapper h2{color:#fff;transition:transform 200ms ease-in-out;border:2px solid #01d5fa;padding:.5em;border-radius:5px}.ship-wrapper h2:hover,.ship-wrapper h2:focus{transform:scale(1.1)}button{background-color:#004450;font-size:1.2em;color:#fff}.ships{position:relative;display:flex;flex-direction:column}.ship{border:2px solid #000;border-radius:5px;margin:5px;height:4vmin;background-color:#01d5fa}.ship-5{width:20vmin}.ship-4{width:16vmin}.ship-3{width:12vmin}.ship-3{width:12vmin}.ship-2{width:8vmin}
